* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #000;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

html {
  scroll-behavior: smooth;
}

#logo {
  stroke: #007bff;
  stroke-width: 2;
}

#logo path:nth-child(1) {
  stroke-dasharray: 210;
  stroke-dashoffset: 210;
  animation: logo 4.5s ease forwards infinite;
}
#logo path:nth-child(2) {
  stroke-dasharray: 148;
  stroke-dashoffset: 148;
  animation: logo 4.5s ease forwards infinite 1s;
}

@keyframes logo {
  20%,
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes child1 {
  from {
    stroke-dashoffset: 210;
  }
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes child2 {
  from {
    stroke-dashoffset: 148;
  }
  to {
    stroke-dashoffset: 0;
  }
}

section {
  padding-top: 60px;
}

.margin-bottom li:last-child {
  margin-bottom: 0 !important;
}

#main-nav .active {
  border-bottom: 2px solid white;
}

.navbar {
  opacity: 0.8;
}

#home-section {
  padding-top: 90px;
  width: 100%;
}

#home-section .label {
  padding: 8px 25px;
  background-color: #007bff;
  border-radius: 20px;
  border-bottom-left-radius: 0;
}

#aboutme-section span,
#experience-section .tech-used span {
  padding: 0.25rem 0.5rem;
  border: 1px solid#007bff;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  display: inline-block;
  margin: 5px;
}

.bar {
  list-style: none;
}

.bar > li {
  position: relative;
}

.bar > li::before {
  content: "\25CF";
  position: absolute;
  top: -5px;
  left: -19px;
  color: #007bff;
}

.bar > li::after {
  content: "";
  border-left: 1px dashed #007bff;
  position: absolute;
  top: 5px;
  left: -15.1px;
  height: 90%;
}
